



































































































import { Component, Prop, Vue } from "vue-property-decorator";

class Todo {
  todo: string;
  checked: boolean;
  constructor(todo: string) {
    this.todo = todo;
    this.checked = false;
  }
}

@Component
export default class Todolist extends Vue {
  todoInput: string = "";
  todoList: Todo[] = [];
  state: number = 0;
  toggleChecked(index: number) {
    this.todoList[index].checked = !this.todoList[index].checked;
  }
  save() {
    window.localStorage.setItem(
      "typeScriptTodos",
      JSON.stringify(this.todoList)
    );
  }
  addTodo() {
    // console.log('TodoInput',this.todoInput);
    if (this.todoInput.length < 1) {
      return;
    }
    let todo = new Todo(this.todoInput);
    this.todoList.push(todo);
    this.todoInput = "";
    this.save();
  }
  delTodo(todo: Todo) {
    // console.log('delTodo',todo);
    this.todoList = this.todoList.filter((todoItem) => {
      return todoItem !== todo;
    });
    this.save();
  }
  editTodo(todo: Todo) {
    // console.log(todo);
    const content = window.prompt("請輸入修改內容");
    // console.log(content);
    if (content?.trim()) {
      todo.todo = content.trim();
      this.save();
    }
  }
  clearTodo() {
    this.todoList = [];
    this.save();
  }
  mounted() {
    const store = window.localStorage.getItem("typeScriptTodos");
    if (store !== null) {
      this.todoList = JSON.parse(store) || [];
    }
  }
  get filterItem() {
    return this.todoList.filter((item) => {
      if (this.state === 0) return true;
      else if (this.state === 1) return !item.checked;
      else return item.checked;
    });
  }
}
